@import "https://fonts.bunny.net/css?family=ibm-plex-sans:200,300,400,500,600,700";
:root {
  font-family: IBM Plex Sans, sans-serif;
  font-size: 12pt;
}

* {
  box-sizing: border-box;
  font-family: inherit;
}

body {
  background-color: #e8e4db;
  max-width: 100vw;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

a {
  color: #2a62cf;
  text-decoration: none;

  &:hover {
    color: #133175;
    text-underline-offset: .23em;
    text-decoration: underline 2px;
  }

  &:focus {
    color: #f6d81a;
    background-color: #0f1c35;
    outline: none;
  }
}

.details {
  border-top: 5px solid #0f1c35;
  align-items: center;
  width: 820px;
  max-width: 100%;
  margin: 0 auto 5vmax;
  padding-top: 2vmax;
  display: flex;

  &:before {
    content: "";
    z-index: -1;
    clip-path: polygon(0 0, 100% 0, 100% 100%, 85% 79%, 71% 79%, 55% 76%, 37% 69%, 11% 83%, 0 100%);
    background: linear-gradient(#2a62cf, #48a3bf);
    width: 100%;
    height: 150px;
    position: absolute;
    top: 0;
    left: 0;
  }

  & h1 {
    color: #0f1c35;
    margin: 0;
    font-size: 28pt;
    font-weight: bold;
  }

  & .subtitle {
    color: #333335;
    font-size: 15pt;
    line-height: 1.33em;
    display: inline;
  }

  & aside {
    margin: 0 2vmax;
  }

  & .profile-pic {
    object-fit: cover;
    border: 2px solid #e5e5e5;
    border-radius: 50%;
    width: 180px;
    max-width: 20vw;
    height: 100%;
    box-shadow: 5px 5px 20px #0000004d;
  }

  & ul {
    gap: 2.33ch;
    margin: 1vmax 0;
    padding: 0;
    display: flex;

    & li {
      align-items: center;
      gap: .8ch;
      list-style: none;
      display: flex;
      position: relative;

      & .icon {
        width: 1.2em;
        height: 1.2em;
        position: relative;
        top: -2px;
      }
    }

    &.skill-tags {
      flex-wrap: wrap;
      gap: 1ch;
      margin: 1.5vmax 0;

      & .tag {
        user-select: none;
        color: #000000c7;
        background-color: #9ee1b1;
        border: 1px solid #0000004d;
        border-radius: .8ch;
        padding: .2em .4em;
        font-size: .89em;
        transition: background-color .5s ease-in-out, box-shadow .5s ease-in-out;
        position: relative;
        overflow: hidden;

        &:after {
          content: "";
          opacity: 0;
          z-index: -1;
          background-image: linear-gradient(90deg, red, orange, #ff0, green, #00f, indigo, violet, red);
          background-size: 300% 100%;
          transition: opacity .5s ease-in-out;
          position: absolute;
          inset: 0;
        }

        &:hover:after {
          opacity: 1;
          animation: 3s linear infinite rainbowScroll;
        }

        &:hover {
          color: #000;
          text-shadow: 0 0 3px #fff;
          background-color: #ffffff59;
          box-shadow: 0 0 20px #ff0;
        }
      }
    }
  }
}

@keyframes rainbowScroll {
  0% {
    background-position: 0%;
  }

  100% {
    background-position: 150%;
  }
}

.main-content {
  align-items: flex-start;
  gap: 3.4vmax;
  width: 1244px;
  max-width: 100%;
  margin: 3vmax auto;
  padding: 0 2vmax;
  display: flex;
  position: relative;
  overflow: hidden;

  & .contents {
    background-color: #fff;
    min-width: 230px;
    padding: 1.66vmax;
    position: sticky;
    top: 2vmax;

    & button {
      appearance: none;
      background: none;
      border: none;
      border-radius: 3px;
      padding: .33em;
      display: none;

      &:hover {
        background-color: #e5e5e5;
      }
    }

    & header {
      justify-content: space-between;
      align-items: flex-start;
      display: flex;

      & button {
        cursor: pointer;
      }
    }

    & h1 {
      margin: 0 0 1.33em;
      font-size: 12pt;
    }

    & a:active {
      font-weight: bold;
    }

    & ul {
      flex-direction: column;
      gap: 1em;
      margin: 0;
      padding: 0;
      list-style: none;
      display: flex;

      & li:not(:last-child) {
        border-bottom: 1px solid #ddd;
        padding-bottom: 1em;
      }
    }
  }

  & main > section {
    border-top: 2px solid #d3d3da;
    margin-bottom: 4vmax;
    padding-top: 1.5vmax;
    position: relative;

    & h1 {
      color: #0f1c35;
      margin-top: 0;
    }
  }
}

.gallery {
  flex-wrap: nowrap;
  gap: 2vmax;
  padding-bottom: 20px;
  display: flex;
  overflow-x: auto;

  & figure {
    background-color: #f5f5f5;
    border: 1px solid #00000059;
    flex-direction: column;
    max-width: 320px;
    margin: 0;
    display: flex;
  }

  & figcaption {
    padding: .33em .67em;
    font-size: .88em;
  }

  & img {
    object-fit: contain;
    aspect-ratio: 4 / 3;
    flex: auto;
    width: 100%;
  }
}

.gallery-item {
  flex: none;
}

@media screen and (width <= 940px) {
  .main-content {
    flex-direction: column;
    display: flex;

    & .contents {
      width: 100%;
      margin-bottom: 3vmax;
      position: relative;

      & button.hide {
        display: block;
      }

      &.closed {
        & button.show {
          display: block;
        }

        & button.hide, & ul {
          display: none;
        }
      }
    }
  }
}

details {
  margin: .5vmax .5vmax 1vmax;

  & summary {
    cursor: pointer;
    color: #0f1c35;
    margin: .56em 0;
    font-size: 13pt;
    font-weight: 500;
  }
}
